import { AjaxHttp } from '../../../js/ajaxHttp'
import ListCell from './ListCell.vue'
import { getYMDFormateTime, isArrayType } from "../../../js/utility.js"

export default {
  name: 'receivable-cell',
  components: {
    ListCell,
  },
  data() {
    return {}
  },
  mounted() {

  },
  computed: {
    create_date() {
      return getYMDFormateTime(this.item.create_time);
    },
    count() {
      return this.item.receivingorder.length;
    }
  },
  props: {
    item: {
      "contract_id": "",
      "receivables_id": "",
      "price": "",
      "create_time": "",
      "ys_receivables_price": "",
      "receivingorder": []
    }
  },
  methods: {
    checkHistory() {
      this.$store.commit('updateArray', this.item.receivingorder);
      this.$router.push({
        name: 'moneyhistory',
        params: {
          mainid: this.item.contract_id,
          id: this.item.receivables_id
        }
      })
    }
  }
}




