import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import { mapState } from 'vuex'
import $ from 'jquery'
import event from '../../../js/Event.js'
import Swiper from '../../../../static/swiper-3.4.2.min.js'
import PlateType from '../../../js/enum.js'
import { AjaxHttp, AjaxImageHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
