import CommonCell from '../components/CommonCell'
import ExamineCell from '../components/ExamineCell.vue'
import MoneyCell from '../components/MoneyCell.vue'
import CommunicationCell from '../components/CommunicationCell.vue'
import ReceivableCell from '../components/ReceivableCell.vue' 
import MreceivingorderCell from '../components/MreceivingorderCell.vue' 
import ListCell from '../components/ListCell'
import { getYMDHMSFormateTime, isArrayType, isNullValue, callPhone, getModuleKeyword, getImageUrl, getHMFormateTime, objToString, getYMDFormateTime, insertToken, updateImageURL, openFile, compressImage, createBlob } from "../../../js/utility.js"

export default {
name: 'maindetail',
components: {
XButton,
Tab,
TabItem,
Popup,
Sticky,
XCircle,
Group,
ContractCell,
BusinessCell,
CommonCell,
ExamineCell,
MoneyCell,
CommunicationCell,
ListCell,
Cell,
Swipeout,
SwipeoutItem,
SwipeoutButton,
ReceivableCell,
MreceivingorderCell
},
data() {
return {
tabType: 0, //当前展示列表类型
tabList: [],
type: '',
tabContenList: [], //展示的列表内容
percent: 0,
topcontent1: '', //顶部展示内容
topcontent2: '',
topcontent3: '',
topcontent: '', //最顶部名称
check_name: '', //商机审核状态
check_name_color: '#999',
status_name: '', //商机状态
detail: {},
show: false,
p: 1, //沟通日志需要
contract_price: {}, //合同下财务信息
product_data: {}, //合同商机下的产品使用
examinePermision: false, //审批权限
permisionStyle: 'default', //默认 defalut 撤回 revocation
}
},
computed: {
PlateType() {
return PlateType;
},



...mapState([
'crmDetailInfo',
'crmListInfo' 
]),
canPhone() { //是否能打电话
return !isNullValue(this.topcontent3);
},
swipeoutArray() {
if (this.tabType === 0 || this.tabType === 4) {
return [{
action: 'delete',
color: "#D23934",
name: '删除'
}];
} else if (this.tabType === 1) {
return [{
action: 'unbind',
color: "#336DD6",
name: '解绑'
}];
}
return [];
},
logImage() {
if (this.type === PlateType.Customer || this.type === PlateType.CustomerPool || this.type === PlateType.Leads) {
return require('../../../assets/customer.png');
} else if (this.type === PlateType.Business) {
return require('../../../assets/business.png');
} else if (this.type === PlateType.Contract) {
return require('../../../assets/contract.png');
} else if (this.type === PlateType.Contacts) {
return require('../../../assets/lxr.png');
} else if (this.type === PlateType.FinanceReceivables) {
return require('../../../assets/receivable.png');
} else if (this.type === PlateType.FinanceReceivingorder) {
return require('../../../assets/returned.png');
}
return '';
},
toptitle1() {
if (this.type === PlateType.Contract) {
return '签约时间';
} else if (this.type === PlateType.Contacts) {
return '所属客户';
} else if (this.type === PlateType.FinanceReceivables) {
return '应收款时间';
} else if (this.type === PlateType.FinanceReceivingorder) {
return '回款时间';
} else {
return '负责人';
}
},
toptitle2() {
if (this.type === PlateType.Business || this.type === PlateType.Contract) {
return '相关客户';
} else if (this.type === PlateType.Contacts) {
return '角色';
} else if (this.type === PlateType.Leads || this.type === PlateType.Customer || this.type === PlateType.CustomerPool) {
return '最后跟进时间';
} else if (this.type === PlateType.FinanceReceivables) {
return '应收款金额';
} else if (this.type === PlateType.FinanceReceivingorder) {
return '回款金额';
}
},
toptitle3() {
if (this.type === PlateType.Business) {
return '商机金额';
} else if (this.type === PlateType.Leads || this.type === PlateType.Customer || this.type === PlateType.CustomerPool || this.type === PlateType.Contacts) {
return '联系电话';
} else if (this.type === PlateType.Contract) {
return '相关商机';
} else if (this.type === PlateType.FinanceReceivables) {
return '相关合同';
} else if (this.type === PlateType.FinanceReceivingorder) {
return '相关应收款';
}
},
topcontent1Color() {
if (this.$route.params.type === PlateType.Leads || this.$route.params.type === PlateType.Customer || this.$route.params.type === PlateType.Contacts || this.$route.params.type === PlateType.Business) {
return '#3496D5';
}
return '#868B8F';
},
topcontent2Color() {
if (this.$route.params.type === PlateType.Business || this.$route.params.type === PlateType.Contract) {
return '#3496D5';
}
return '#868B8F';
},
topcontent3Color() {
if (this.$route.params.type === PlateType.Leads || this.$route.params.type === PlateType.Customer || this.$route.params.type === PlateType.Contacts || this.$route.params.type === PlateType.Contract || this.type === PlateType.FinanceReceivables || this.type === PlateType.FinanceReceivingorder) {
return '#3496D5';
}
return '#868B8F';
},
quickAddList() {
var infos = {
0: { 'img': require("../../../assets/add_log.png"), 'title': '添加日志', 'type': '0' }, //字符串传递中稳定
1: { 'img': require("../../../assets/add_sign.png"), 'title': '添加签到', 'type': '1' },
2: { 'img': require("../../../assets/add_remind.png"), 'title': '添加提醒', 'type': '2' },
3: { 'img': require("../../../assets/add_business.png"), 'title': '添加商机', 'type': '3' },
4: { 'img': require("../../../assets/add_contract.png"), 'title': '添加合同', 'type': '4' },
5: { 'img': require("../../../assets/add_payments.png"), 'title': '添加回款', 'type': '5' },
6: { 'img': require("../../../assets/add_files.png"), 'title': '添加附件', 'type': '6' },
7: { 'img': require("../../../assets/leads_change.png"), 'title': '线索转换', 'type': '7' },
8: { 'img': require("../../../assets/putinpool.png"), 'title': '放入线索池', 'type': '8' },
9: { 'img': require("../../../assets/customer_receive.png"), 'title': '客户领取', 'type': '9' },
10: { 'img': require("../../../assets/customer_distribute.png"), 'title': '客户分配', 'type': '10' },
11: { 'img': require("../../../assets/add_business.png"), 'title': '关联商机', 'type': '11' },
12: { 'img': require("../../../assets/add_receivable.png"), 'title': '添加应收款', 'type': '12' },
};

var tlists = [];
if (this.type === PlateType.Customer) {
tlists = [0, 1, 3, 6];
} else if (this.type === PlateType.CustomerPool) {
tlists = [0, 9, 10];
} else if (this.type === PlateType.Leads) {
tlists = [0, 7]; 
} else if (this.type === PlateType.Business) {
tlists = [0, 6];
} else if (this.type === PlateType.Contract) {
if (this.detail.is_checked === '1') {
tlists = [6, 12];
} else {
tlists = [6];
}
} else if (this.type === PlateType.Contacts) {
tlists = [11];
} else if (this.type === PlateType.FinanceReceivables) {
tlists = [5];
}

var lists = [];
for (var i = 0; i < tlists.length; i++) {
if (lists.length === 0) {
lists.push([]);
} else if (lists[lists.length - 1].length === 4) {
lists.push([]);
}

lists[lists.length - 1].push(infos[tlists[i]]);
}
return lists;
}
},
mounted() {
this.type = this.$route.params.type;
this.initInfo();
},
beforeRouteLeave(to, from, next) {

















var name = this.$route.params.type + "@" + this.$route.params.id;
this.saveCache(from);

if (to.name === 'selectlist') { //跳转选择列表  或者  crm选择列表
event.$on('selectedRelateItem', function(data) {
this.handleSelectedInfo(data);
}.bind(this));
} else if (to.name === 'relatelist') {
event.$on('selectedRelateItem', function(data) {
this.handleRelateInfo(data);
}.bind(this));
}

next();
},
beforeRouteUpdate(to, from, next) {
var name = this.$route.params.type + "@" + this.$route.params.id;
this.saveCache(from);
this.$route.params.type = to.params.type;
this.$route.params.id = to.params.id;

this.tabType = 0; 
this.tabList = [];
this.tabContenList = []; 
this.percent = 0;
this.topcontent1 = ''; 
this.topcontent2 = '';
this.topcontent3 = '';
this.topcontent = ''; 
this.check_name = ''; 
this.check_name_color = '#999';
this.status_name = ''; 
this.detail = {};
this.show = false;
this.p = 1; 
this.contract_price = {}; 
this.product_data = {}; 
this.examinePermision = false;
this.permisionStyle = 'default';
this.initInfo();
next();
},
beforeRouteEnter(to, from, next) {


if (from.name === 'selectlist' || from.name === 'relatelist') { //跳转选择列表  或者  crm选择列表
event.$off("selectedRelateItem");
}
next();
},
methods: {
initInfo() {
this.type = this.$route.params.type;

this.$nextTick(() => {
this.lunbo();
});


var tabAllList = {
0: { 'title': '沟通日志', 'type': 0 },
1: { 'title': '商机', 'type': 1 },
2: { 'title': '合同', 'type': 2 },
3: { 'title': '财务', 'type': 3 },
4: { 'title': '附件', 'type': 4 },
5: { 'title': '阶段记录', 'type': 5 },
6: { 'title': '产品', 'type': 6 },
7: { 'title': '审核历史', 'type': 7 },
8: { 'title': '修改记录', 'type': 8 },
9: { 'title': '联系人', 'type': 9 },
10: { 'title': '回款', 'type': 10 },
};
var tlists = [];
if (this.type === PlateType.Customer) {
tlists = [0, 1, 2, 3, 4];
this.tabType = 0;
} else if (this.type === PlateType.CustomerPool) {
tlists = [0, 4];
this.tabType = 0;
} else if (this.type === PlateType.Leads) {
tlists = [0, 4];
this.tabType = 0;
} else if (this.type === PlateType.Business) {
tlists = [0, 9, 2, 6, 3, 4];
this.tabType = 0;
} else if (this.type === PlateType.Contract) {
tlists = [3, 6, 7, 4];
this.tabType = 3;
} else if (this.type === PlateType.Contacts) {
tlists = [1];
this.tabType = 1;
} else if (this.type === PlateType.FinanceReceivables) {
tlists = [10];
this.tabType = 10;
} else if (this.type === PlateType.FinanceReceivingorder) {
tlists = [7];
this.tabType = 7;
}

var tempList = [];
for (var i = 0; i < tlists.length; i++) {
tempList.push(tabAllList[tlists[i]]);
}
this.tabList = tempList;



var name = this.$route.params.type + "@" + this.$route.params.id;
var cacheItem = this.crmDetailInfo[name];

if (cacheItem && cacheItem.type === this.type && cacheItem.id === this.$route.params.id) {
this.tabType = cacheItem.tabType;
this.examinePermision = cacheItem.examinePermision;
this.permisionStyle = cacheItem.permisionStyle;
this.handleDetail(cacheItem.detail);
if (cacheItem.refreshTop) { //回款审批需要刷新
this.getDetail();
}
if (cacheItem.refresh) { //是 && this.tabType === 0沟通日志暂时去掉 全部页面可刷新
this.p = 1;
cacheItem.refresh = false;
this.$store.commit('updateCrmDetailInfo', this.crmDetailInfo);
this.tabContenList = [];

} else {
this.p = cacheItem.p;
this.tabContenList = cacheItem.tabContenList;
this.contract_price = cacheItem.appendInfo;
this.product_data = cacheItem.product_data;
this.$nextTick(() => {
this.$refs.my_scroller.resize();
this.$refs.my_scroller.scrollTo(0, cacheItem.scroll, false);
if (cacheItem.loadingState === 2) {
this.$refs.my_scroller.finishInfinite(true);
}
});
}
} else {
this.getDetail();
this.$nextTick(() => {
this.tabContenList = [];
this.$refs.my_scroller.finishInfinite(false);
});

}

},
lunbo() { //快捷添加数据
var swiper = new Swiper('.swiper-container', {
pagination: '.swiper-pagination',
paginationClickable: true
});
},
hideSwiper() {
this.show = false;
$(".am-share").removeClass("am-modal-active");
},
showSwiper() {
this.show = true;
$(".am-share").addClass("am-modal-active");
},
businessStateClick() { //商机状态推入
this.$router.push({
name: 'selectlist',
params: {
id: this.$route.params.id,
type: this.$route.params.type,
selecttype: '1',
section: '-1',
row: '-1',
}
})
},
onTabItemClick(type) { //相关模块被点击
if (type === 0) { //沟通日志列表特殊对待
this.p = 1;
}
this.tabType = type;
this.tabContenList = [];
this.$refs.my_scroller.finishInfinite(false);
},

getRelateList(done) { //相关列表
AjaxSilentHttp('POST', this.getUrl(this.tabType), this.getPostParams(this.tabType), this, function(res, wself, success) {
if (wself.tabType === 7 && wself.type === PlateType.FinanceReceivingorder) {
if (success && res.data) {
wself.tabContenList = [];
wself.tabContenList.push({ title: res.data.owner_info.full_name, check_time: getYMDHMSFormateTime(res.data.res_info.create_time), is_checked: '', check_color: 'white', content: '', img: getImageUrl(res.data.owner_info.thumb_path) });

var full_name = '财务审核员';
var thumb_path = '';
if (!isArrayType(res.data.examine_info) && res.data.examine_info.full_name) {
full_name = res.data.examine_info.full_name;
thumb_path = getImageUrl(res.data.examine_info.thumb_path);
}
wself.tabContenList.push({ title: full_name, check_time: getYMDHMSFormateTime(res.data.res_info.check_time), is_checked: wself.getCheckname(res.data.res_info.status), check_color: wself.getCheckColor(res.data.res_info.status), content: res.data.res_info.check_des, img: thumb_path });
}
} else {
if (success && isArrayType(res.list)) {
if (wself.tabType === 1) { //商机
var newItems = [];
for (var i = 0; i < res.list.length; i++) {
var item = res.list[i];
item.create_time = getYMDFormateTime(item.create_time);
item.id = item.business_id;
newItems.push(item);
}
wself.tabContenList = newItems;
} else if (wself.tabType === 2 || wself.tabType === 7 || wself.tabType === 9) { //合同 （9 联系人）
var newItems = [];
for (var i = 0; i < res.list.length; i++) {
newItems[i] = wself.getNewItem(res.list[i]);
}
wself.tabContenList = newItems;
} else {
if (wself.type === PlateType.Contract && wself.tabType === 3) { //合同下财务回款
wself.contract_price = res.price;
for (var i = 0; i < res.list.length; i++) { //把合同ID 交于回款
var item = res.list[i];
item.contract_id = wself.$route.params.id;
}
} else if (wself.type === PlateType.FinanceReceivables && wself.tabType === 10) { //应收款下回款
wself.contract_price.receivables_price_all = res.receivingorder_money;
wself.contract_price.sub_receivables_price_all = res.un_receivables_money;
}
if (wself.tabType === 6) {
wself.product_data = res.data;
}
wself.tabContenList = res.list;
}
}
}

done(true);
});
},
getUrl(type) { //相关url
if (type === 0) {
return 'm=index&a=loglist';
} else if (type === 1) { //商机
return 'm=business&a=index';
} else if (type === 2) { //合同
return 'm=contract&a=index';
} else if (type === 3) { //财务
if (this.$route.params.type === PlateType.Contract) {
return 'm=contract&a=finance'; 
} else if (this.$route.params.type === PlateType.Business || this.$route.params.type === PlateType.Customer) {
return 'm=finance&a=index'; 
}
} else if (type === 4) { //附件
return 'm=index&a=filelist';
} else if (type === 7) { //审核历史
if (this.type === PlateType.Contract) {
return 'm=contract&a=checkList';
} else if (this.type === PlateType.FinanceReceivingorder) {
return 'm=finance&a=check_list';
}
} else if (type === 9) { //联系人
return 'm=contacts&a=index';
} else if (type === 6) { //产品
return 'm=contract&a=product';
} else if (type === 10) { //回款
return 'm=finance&a=index';
}

return '';
},
getPostParams(type) { //相关参数

if (type === 1 || type === 2) { //商机 合同
if (this.type === PlateType.Customer) {
return { 'customer_id': this.$route.params.id };
} else if (this.type === PlateType.Contacts) {
return { 'contacts_id': this.$route.params.id };
} else if (this.type === PlateType.Business) {
return { 'business_id': this.$route.params.id };
}
} else if (type === 4) { //附件
return {
'id': this.$route.params.id,
'module': getModuleKeyword(this.type)
};
} else if (type === 7 || type === 3) { //审核历史  3 合同下的财务
if (type === 3 && this.$route.params.type === PlateType.Business) {
return {
'business_id': this.$route.params.id,
't': 'receivingorder',
};
} else if (type === 3 && this.$route.params.type === PlateType.Customer) {
return {
'customer_id': this.$route.params.id,
't': 'receivingorder',
};
} else if (type === 7 && this.$route.params.type === PlateType.FinanceReceivingorder) {
return {
'id': this.$route.params.id,
't': 'receivingorder',
};
} else {
return {
'id': this.$route.params.id
};
}
} else if (type === 9) { //联系人
return {
'business_id': this.$route.params.id
};
} else if (type === 6) { //产品
if (this.$route.params.type === PlateType.Business) {
return {
'business_id': this.$route.params.id
};
} else if (this.$route.params.type === PlateType.Contract) {
return {
'contract_id': this.$route.params.id
};
}
} else if (type === 10) {
return {
'receivables_id': this.$route.params.id,
't': 'receivingorder',
};
}
return {};
},
getNewItem(item) {
if (this.tabType === 2) { //合同
return { title: item.number, detail: item.customer_name, rightTitle: getYMDFormateTime(item.create_time), rightDetail: item.price, percent: item.schedule.toString(), permission: item.permission, id: item.contract_id };
} else if (this.tabType === 7) { //合同审核历史
return { title: item.user.full_name, check_time: getYMDHMSFormateTime(item.check_time), is_checked: this.getCheckname(item.is_checked), check_color: this.getCheckColor(item.is_checked), img: getImageUrl(item.user.thumb_path), content: item.content, id: item.check_id };
} else if (this.tabType === 9) { //联系人
return { title: item.name, detail: item.customer_name, rightTitle: item.telephone, permission: item.permission, id: item.contacts_id };
}
},
getCheckColor(check) {
if (check === '0') {
return '#F5CA00';
} else if (check === '1') {
return '#7CCA4E';
} else if (check === '2') {
return '#F5715F';
}
},
getCheckname(check) {
if (check === '0') {
return '待审';
} else if (check === '1') {
return '通过';
} else if (check === '2') {
return '驳回';
}
},
enterRelateDetail(item) {
var type = '';
if (this.tabType === 1) { //商机
type = PlateType.Business;
} else if (this.tabType === 2) { //合同
type = PlateType.Contract;
} else if (this.tabType === 9) { //联系人
type = PlateType.Contacts;
}

this.$router.push({
name: 'maindetail',
params: {
id: item.id,
type: type
}
});
},
infinite(done) {
if (this.tabType === 0) {
this.getCommunicatelogList(done);
} else {
this.getRelateList(done);
}
},
refresh(done) {
this.getDetail();
this.p = 1;
if (this.tabType === 0) {
this.getCommunicatelogList(done);
} else {
this.getRelateList(done);
}
},

getCommunicatelogList(done) {
AjaxSilentHttp('POST', 'm=index&a=loglist', {
'module': getModuleKeyword(this.type),
'id': this.$route.params.id,
'p': this.p
}, this, function(res, wself, success) {
if (success && wself.tabType === 0) {
if (wself.p === 1) {
wself.tabContenList = res.list;
} else {
wself.tabContenList = wself.tabContenList.concat(res.list);
}

if (typeof done === 'function') {
if (wself.p == 1 && res.list.length < 10) {
done(true);
} else if (res.list.length === 0) {
done(true);
} else {
done();
}
}
wself.p = wself.p + 1;
} else {
done(true);
}

});
},
fileTypeImage(item) {
var type = item.file_type;
if (type === 'doc' || type === 'DOC' || type === 'docx') {
return require("../../../assets/file/doc.png");
} else if (type === 'xls' || type === 'xlsx') {
return require("../../../assets/file/excel.png");
} else if (type === 'png' || type === 'gif' || type === 'jpg' || type === 'jpeg') {
return getImageUrl(item.file_path);
} else if (type === 'pdf' || type === 'PDF') {
return require("../../../assets/file/pdf.png");
} else if (type === 'ppt' || type === 'PPT' || type === 'pptx') {
return require("../../../assets/file/ppt.png");
} else if (type === 'txt' || type === 'TXT') {
return require("../../../assets/file/txt.png");
} else if (type === 'zip' || type === 'ZIP' || type === 'rar') {
return require("../../../assets/file/zip.png");
}
return require("../../../assets/file/unknown.png");

},

filepreview(url) {
openFile(getImageUrl(url));




},
uploadimg(e) {
var src, url = window.URL || window.webkitURL || window.mozURL,
files = e.target.files;
for (var i = 0, len = files.length; i < len; ++i) {
var file = files[i];





var self = this;
compressImage(file, 0.3, (code) => {
var formdata = new FormData();
formdata.append('file', createBlob(code), file.name);
if (self.type === PlateType.Customer) {
formdata.append('module', 'customer');
formdata.append('r', 'rCustomerFile');
formdata.append('id', self.$route.params.id);
} else if (self.type === PlateType.Business) {
formdata.append('module', 'business');
formdata.append('r', 'rBusinessFile');
formdata.append('id', self.$route.params.id);
} else if (self.type === PlateType.Contract) {
formdata.append('module', 'contract');
formdata.append('r', 'rContractFile');
formdata.append('id', self.$route.params.id);
}
AjaxImageHttp('POST', 'm=file&a=add', formdata, self, function(res, wself, success) {
if (success) {
if (wself.tabType === 4) { //是在附件加
wself.tabContenList = [];
wself.$refs.my_scroller.finishInfinite(false);
}
}
});
});

}
},
enterDetail() {
this.$router.push({
name: 'crmdetail',
params: {
id: this.$route.params.id,
type: this.type
}
});
},
getDetail() {
var url = '';
var postParams = {};
postParams['id'] = this.$route.params.id;
if (this.type === PlateType.FinanceReceivables) {
url = 'm=finance&a=view';
postParams['t'] = 'receivables';
} else if (this.type === PlateType.FinanceReceivingorder) {
url = 'm=finance&a=view';
postParams['t'] = 'receivingorder';
} else {
url = 'm=' + getModuleKeyword(this.type) + '&a=dynamic';
}
AjaxSilentHttp('POST', url, postParams, this, function(res, wself, success) {
if (success) {
wself.handleDetail(res.data);
wself.handlePermission(res);
}
});
},
getLoadingDetail() {
var url = '';
var postParams = {};
postParams['id'] = this.$route.params.id;
if (this.type === PlateType.FinanceReceivables) {
url = 'm=finance&a=view';
postParams['t'] = 'receivables';
} else if (this.type === PlateType.FinanceReceivingorder) {
url = 'm=finance&a=view';
postParams['t'] = 'receivingorder';
} else {
url = 'm=' + getModuleKeyword(this.type) + '&a=dynamic';
}
AjaxHttp('POST', url, postParams, this, function(res, wself, success) {
if (success) {
wself.handleDetail(res.data);
wself.handlePermission(res);
}
});
},
handlePermission(data) {
if (data.permission) {
if (this.type === PlateType.FinanceReceivingorder) {
if (data.permission.check === 1) {
this.examinePermision = true;
if (data.data.status === '1' || data.data.status === '2') {
this.permisionStyle = 'revocation';
} else if (data.data.status === '0') {
this.permisionStyle = 'default';
}
} else {
this.examinePermision = false;
}
}
}
},
handleDetail(detail) {
this.detail = detail;
if (this.type === PlateType.Leads) {
this.topcontent = detail.name;
this.topcontent1 = detail.owner.full_name;
this.topcontent2 = getYMDHMSFormateTime(detail.update_time);
this.topcontent3 = detail.mobile;
} else if (this.type === PlateType.Customer) {
this.topcontent = detail.name;
this.topcontent1 = detail.owner.full_name;
this.topcontent2 = getYMDHMSFormateTime(detail.update_time);
this.topcontent3 = detail.contacts_telephone;
} else if (this.type === PlateType.CustomerPool) {
this.topcontent = detail.name;
this.topcontent1 = '';
this.topcontent2 = getYMDHMSFormateTime(detail.update_time);
this.topcontent3 = detail.contacts_telephone;
} else if (this.type === PlateType.Business) {
this.topcontent = detail.name;
this.topcontent1 = detail.owner.full_name;
this.topcontent2 = detail.customer_name;
this.topcontent3 = detail.final_price + '元';
this.status_name = detail.status_name;
this.percent = detail.schedule;
} else if (this.type === PlateType.Contract) {
this.topcontent = detail.contract_name;
this.topcontent1 = getYMDHMSFormateTime(detail.create_time);
this.topcontent2 = detail.customer_name;
this.topcontent3 = detail.business_name;
this.check_name = detail.check_name;
if (this.check_name === '通过') { this.check_name_color = '#7CCA4E' } else if (this.check_name === '驳回') { this.check_name_color = '#F5715F' } else if (this.check_name === '待审') { this.check_name_color = '#F5CA00' }
} else if (this.type === PlateType.Contacts) {
this.topcontent = detail.name;
this.topcontent1 = detail.customer_name;
this.topcontent2 = detail.role;
this.topcontent3 = detail.telephone;
} else if (this.type === PlateType.FinanceReceivables) {
this.topcontent = detail.name;
this.topcontent1 = getYMDFormateTime(detail.pay_time);
this.topcontent2 = detail.price + '元';
this.topcontent3 = detail.contract.number;
} else if (this.type === PlateType.FinanceReceivingorder) {
this.topcontent = detail.name;
this.topcontent1 = getYMDFormateTime(detail.pay_time);
this.topcontent2 = detail.money + '元';
this.topcontent3 = detail.receivables_name;
}
},

clickQuickAdd(item) {

this.hideSwiper();
if (item.type === '0' || item.type === '12') { //0 添加日志 12 添加应收款
this.$router.push({
name: 'addrelate',
params: {
id: this.$route.params.id,
mainid: this.$route.params.id,
type: this.$route.params.type,
addtype: item.type
}
});
} else if (item.type === '8' || item.type === '7') { //8放入线索池 7线索转换
var url = '';
if (item.type === '8') {
url = 'm=leads&a=remove';
} else if (item.type === '7') {
url = 'm=leads&a=changecustomer';
}
AjaxHttp('POST', url, {
'id': this.$route.params.id,
}, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
if (res.status === 1) {
wself.deleteMainList();
wself.$router.go(-1);
}
}
});
} else if (item.type === '9' || item.type === '10') { //9客户领取
if (item.type === '9') {
AjaxHttp('POST', 'm=customer&a=receive', {
'id': this.$route.params.id,
'type': '1',
}, this, function(res, wself, success) {
if (success) {
if (res.status === 1) {
wself.$router.go(-1);
}
}
});
} else if (item.type === '10') { //10 客户分配
this.$router.push({
name: 'selectlist',
params: {
id: this.$route.params.id,
type: this.$route.params.type,
selecttype: '2', //负责人
section: '-1',
row: '-1',
}
})
}
} else if (item.type === '6') { //6 附件添加



if ($(fileElem)) {
$(fileElem).click();
}

} else if (item.type === '11') { //11 关联商机
this.$router.push({
name: 'relatelist',
params: {
addition: { customer_id: this.detail.customer_id, contacts_id: this.$route.params.id },
type: PlateType.Business,
fromtype: this.$route.params.type,
action: 'select',
section: '-1',
row: '-1',
}
})
} else if (item.type === '3') { //3 添加商机
this.$store.commit('updateDetail', {
type: 'custombusiness',
id: this.$route.params.id,
name: this.detail.name,
});
this.$router.push({ name: 'adddata', params: { type: PlateType.Business, action: 'custombusiness', id: '0' } });
} else if (item.type === '1') { //1 添加签到
this.$store.commit('updateDetailView', {
type: 'fieldsigncustomer', //客户签到
id: this.$route.params.id,
name: this.detail.name,
});
if (this.tabType === 0) { //从沟通日志进入签到默认刷新
var name = this.$route.params.type + "@" + this.$route.params.id;
var cacheItem = this.crmDetailInfo[name];
if (cacheItem) {
cacheItem.refresh = true;
} else {
this.crmDetailInfo[name] = { refresh: true };
}
this.$store.commit('updateCrmDetailInfo', this.crmDetailInfo);
}
this.$router.push({ name: 'fieldsign', params: { action: 'customer' } });
} else if (item.type === '5') { //5 添加回款
this.$router.push({
name: 'addrelate',
params: {
mainid: this.$route.params.id,
id: this.$route.params.id,
type: PlateType.FinanceReceivables,
addtype: '5'
}
});
}
},
distributionCustomer(owner_role_id) { //分配客户
AjaxHttp('POST', 'm=customer&a=receive', {
'id': this.$route.params.id,
'type': '1',
'owner_role_id': owner_role_id,
}, this, function(res, wself, success) {
if (success) {
if (res.status === 1) {
wself.$router.go(-1);
}
}
});
},
phoneClick() {
callPhone(this.topcontent3);
},
topcontentClick(position) {
if (position === 1) {
if (this.type === PlateType.Leads || this.type === PlateType.Customer || this.type === PlateType.Business) {
if (!isNullValue(this.detail.owner_role_id) && this.detail.owner_role_id !== '0') {
this.$router.push({
name: 'user',
params: {
id: this.detail.owner_role_id
}
})
}
} else if (this.type === PlateType.Contacts) {
if (!isNullValue(this.detail.customer_id) && this.detail.customer_id !== '0') {
this.$router.push({
name: 'maindetail',
params: {
id: this.detail.customer_id,
type: PlateType.Customer
}
});
}
}
} else if (position === 2) {
if (this.type === PlateType.Business || this.type === PlateType.Contract) {
if (!isNullValue(this.detail.customer_id) && this.detail.customer_id !== '0') {
this.$router.push({
name: 'maindetail',
params: {
id: this.detail.customer_id,
type: PlateType.Customer
}
});
}
}
} else if (position === 3) {
if (this.type === PlateType.Contract) {
if (!isNullValue(this.detail.business_id) && this.detail.business_id !== '0') {
this.$router.push({
name: 'maindetail',
params: {
id: this.detail.business_id,
type: PlateType.Business
}
});
}
} else if (this.type === PlateType.FinanceReceivables) {
if (!isNullValue(this.detail.contract.contract_id) && this.detail.contract.contract_id !== '0') {
this.$router.push({
name: 'maindetail',
params: {
id: this.detail.contract.contract_id,
type: PlateType.Contract
}
});
}
} else if (this.type === PlateType.FinanceReceivingorder) {
if (!isNullValue(this.detail.receivables_id) && this.detail.receivables_id !== '0') {
this.$router.push({
name: 'maindetail',
params: {
id: this.detail.receivables_id,
type: PlateType.FinanceReceivables
}
});
}
}
}
},
onSwipeoutClick(type, item, index) { //edit delete
if (type === 'delete') {
var url = '';
var id = '';
if (this.tabType === 0) { //沟通日志删除
url = 'm=log&a=delete';
id = item.log_id;
} else if (this.tabType === 4) { //附件删除
url = 'm=file&a=delete';
id = item.file_id;
}

AjaxHttp('POST', url, {
'id': id,
'module': getModuleKeyword(this.type)
}, this, function(res, wself, success) {
if (success) {
if (res.status === 1) {
wself.tabContenList.splice(index, 1);
}
}
});
} else if (type === 'unbind') {
AjaxHttp('POST', 'm=contacts&a=relToBusiness', { act_n: '0', business_id: item.business_id, contacts_id: this.$route.params.id }, this, function(res, wself, success) {
if (success) {
if (res.status === 1) {
wself.tabContenList.splice(index, 1);
}
}
});
}
},
handleSelectedInfo(data) { //商机推进状态操作

if (this.$route.params.type === PlateType.Business) {
if (data.type === '1') { //商机推进
this.status_name = data.item.title;
this.detail.status_name = data.item.title;
}
} else if (this.$route.params.type === PlateType.CustomerPool) {
if (data.type === '2') { //客户分配
this.distributionCustomer(data.role_id);
}
}
},
handleRelateInfo(data) {
if (data.fromtype === PlateType.Contacts) {
if (data.type === PlateType.Business) { //商机关联
AjaxHttp('POST', 'm=contacts&a=relToBusiness', { act_n: '1', business_id: data.item.id, contacts_id: data.addition.contacts_id }, this, function(res, wself, success) {
if (success) {}
});
}
}
},
swipeoutDisabled(item) { //是否能侧滑
if (this.type === PlateType.Customer && (this.tabType === 1 || (this.tabType === 0 && item.type === 2))) { //2为签到
return true;
}
return false;
},
enterUser(role_id) {
this.$router.push({
name: 'user',
params: {
id: role_id
}
})
},
getUserImage(url) {
return getImageUrl(url);
},
getTime(time) {
return getHMFormateTime(time);
},
previewerImg(images, imgindex) {
var previewerlist = [];
for (var i = 0; i < images.length; i++) {
previewerlist.push({
src: getImageUrl(images[i])
});
}
event.$emit('previewerAction', { list: previewerlist, index: imgindex });
},
deleteMainList() { //转换等操作删除列表数据
var index = -1;
for (var i = 0; i < this.crmListInfo.list.length; i++) {
var item = this.crmListInfo.list[i];
if (item.id === this.$route.params.id) {
index = i;
}
}

if (index >= 0) {
this.crmListInfo.list.splice(index, 1);
this.$store.commit('updateCrmListInfo', this.crmListInfo);
}
},
saveCache(router) { //保存缓存
var name = router.params.type + "@" + router.params.id;
var cacheItem = this.crmDetailInfo[name];
if (cacheItem) {
cacheItem = {
type: router.params.type, //平台类型与type相同 进行赋值
id: router.params.id,
detail: this.detail,
tabType: this.tabType,
tabContenList: this.tabContenList,
p: this.p,
examinePermision: this.examinePermision,
permisionStyle: this.permisionStyle,
refresh: cacheItem.refresh,
scroll: this.$refs.my_scroller.getPosition().top,
loadingState: this.$refs.my_scroller.loadingState,
appendInfo: this.contract_price, //暂时存合同应收款信息
product_data: this.product_data,
};
} else {
cacheItem = {
type: router.params.type, //平台类型与type相同 进行赋值
id: router.params.id,
detail: this.detail,
tabType: this.tabType,
tabContenList: this.tabContenList,
p: this.p,
examinePermision: this.examinePermision,
permisionStyle: this.permisionStyle,
refresh: false,
scroll: this.$refs.my_scroller.getPosition().top,
loadingState: this.$refs.my_scroller.loadingState,
appendInfo: this.contract_price, //暂时存合同应收款信息
product_data: this.product_data,
};
}
this.crmDetailInfo[name] = cacheItem;
this.$store.commit('updateCrmDetailInfo', this.crmDetailInfo);
},


















examineOpinion(action) {
if (action === 'revocation') {
AjaxHttp('POST', 'm=finance&a=revokeCheck', { id: this.$route.params.id, t: 'receivingorder' }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.tabContenList = [];
wself.$refs.my_scroller.finishInfinite(false);
wself.getLoadingDetail();
}
});
} else {
this.$router.push({
name: 'financeopinion',
params: {
id: this.$route.params.id,
action: action,
type: this.type,
}
});
}
},
}
}




