import { getYMDHMSFormateTime, getImageUrl, isNullValue } from '../../../js/utility.js'
import PlateType from '../../../js/enum.js'

const EVENT_CLICK = 'click'
export default {
  name: 'communication-cell',
  components: {},
  data() {
    return {
      showNexttime: {
        type: Boolean,
        default: false
      },
      showBussiness: {
        type: Boolean,
        default: false
      },
      showLogstatus: {
        type: Boolean,
        default: false
      },
    }
  },
  computed: {
    imageUrl() {
      return getImageUrl(this.item.owner.thumb_path);
    },
    create_date() {
      return getYMDHMSFormateTime(this.item.create_date);
    },
    nextstep_time() {
      return getYMDHMSFormateTime(this.item.nextstep_time);
    },
  },
  props: {
    item: {
      "content": "",
      "create_date": "",
      "owner": {
        "user_name": "",
        "thumb_path": "",
        "role_name": "",
        "department_name": ""
      }
    },
    plateType: {
      type: String,
      default: PlateType.Default
    },
  },
  mounted() {
    if (this.plateType === PlateType.Leads) {
      this.showNexttime = false;
      this.showBussiness = false;
      this.showLogstatus = false;
    } else if (this.plateType === PlateType.Customer || this.plateType === PlateType.CustomerPool) {
      this.showNexttime = true;
      this.showBussiness = true;
      this.showLogstatus = true;
    } else if (this.plateType === PlateType.Business) {
      this.showNexttime = true;
      this.showBussiness = false;
      this.showLogstatus = true;
    }
  },
  methods: {
    click() {
      if (!isNullValue(this.item.business_id)) {
        this.$emit(EVENT_CLICK, this.item);
        this.$router.push({
          name: 'maindetail',
          params: {
            id: this.item.business_id,
            type: PlateType.Business
          }
        });
      }
    }
  }
}




